import './Card.scss';
import React from 'react';

export const Card = (props) => {
    const { filling, servings, mouses, isCustomerHappy, weight, isActive, onToggleActiveItem, disabled } = props;

    let fillingText, mouseText, cardMessage;
    if(filling === 'fish') {
        fillingText = ' ';
        cardMessage = '      .';
    }
    if(filling === 'foiegras') {
        fillingText = ' -';
        cardMessage = '    .';
    }
    if(filling === 'chicken') {
        fillingText = ' ';
        cardMessage = '      .';
    }
    //      10,  :
    if( mouses === 1) {
        mouseText = ''
    }
    if( mouses === 2 || mouses > 2 ) {
        mouseText = ''
    }
    if( mouses === 5 || mouses > 5) {
        mouseText = ''
    }

    if(!isActive) {
        cardMessage = <span> ?  , <i></i></span>
    }

    if(disabled) {
        cardMessage = `, ${fillingText} `
    }
    return (
        <div className={`card${isActive ? ' card--selected' : ''}${disabled ? ' card--disabled' : ''}`} onClick={onToggleActiveItem}>
            <div className='card__head'>
                <span className='card__text'>  </span>
            </div>
            <div className="card__body">
                <h3 className='card__title'></h3>
                <h4 className='card__filling'>{fillingText}</h4>
                <div className='card__dis'>
                    <span><i>{servings}</i> </span>
                    <span><i>{mouses !== 1 ? mouses : ''}</i> {mouseText}  </span>
                    {isCustomerHappy ? <span> </span> : null}
                </div>
                <div className='card__weight'>
                    <i>{weight}</i>
                    <span></span>
                </div>
            </div>  
            <div className={`card__message${disabled ? ' card__message--is-over' : ''}`}>{cardMessage}</div>
        </div>
    );
}